.triangle-brush-panel {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 8px;
  padding: 12px;
  margin: 8px 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.triangle-brush-panel h4 {
  margin: 0 0 8px 0;
  font-size: 14px;
  color: #333;
  font-weight: 600;
}

.brush-type-controls {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6px;
  margin-bottom: 15px;
}

.brush-type-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 12px;
  border: 2px solid #ddd;
  border-radius: 6px;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
  font-weight: 500;
}

.brush-type-btn:hover {
  background: #f5f5f5;
  border-color: #bbb;
  transform: translateY(-1px);
}

.brush-type-btn.active {
  background: #e3f2fd;
  border-color: #2196f3;
  box-shadow: 0 2px 6px rgba(33, 150, 243, 0.3);
  color: #2196f3;
}

.brush-size-controls {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 8px;
  align-items: center;
  margin-bottom: 8px;
}

.size-btn {
  padding: 8px 12px;
  border: 2px solid #ddd;
  border-radius: 6px;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 16px;
  font-weight: bold;
  min-width: 40px;
}

.size-btn:hover {
  background: #f5f5f5;
  border-color: #bbb;
}

.size-display {
  padding: 8px 12px;
  background: #f8f8f8;
  border: 1px solid #ddd;
  border-radius: 6px;
  text-align: center;
  font-size: 12px;
  font-weight: 500;
  color: #333;
}

.size-info {
  font-size: 11px;
  color: #666;
  text-align: center;
  margin-bottom: 15px;
}

.line-mode-section {
  margin-top: 15px;
  padding-top: 12px;
  border-top: 1px solid #eee;
}

.line-mode-controls {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6px;
}

.mode-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 12px;
  border: 2px solid #ddd;
  border-radius: 6px;
  background: white;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 12px;
  font-weight: 500;
}

.mode-btn:hover {
  background: #f5f5f5;
  border-color: #bbb;
  transform: translateY(-1px);
}

.mode-btn.active {
  background: #fff3e0;
  border-color: #ff9800;
  color: #ff9800;
  box-shadow: 0 2px 6px rgba(255, 152, 0, 0.3);
}

/* 태블릿용 반응형 스타일 */
@media (max-width: 1024px) and (min-width: 768px) {
  .triangle-brush-panel {
    padding: 16px;
    margin: 12px 0;
  }

  .triangle-brush-panel h4 {
    font-size: 18px;
    margin: 0 0 12px 0;
  }

  .brush-type-controls {
    gap: 10px;
    margin-bottom: 20px;
  }

  .brush-type-btn {
    padding: 12px 16px;
    font-size: 16px;
    min-height: 50px;
    border-radius: 8px;
    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;
  }

  .brush-size-controls {
    gap: 12px;
    margin-bottom: 12px;
  }

  .size-btn {
    padding: 12px 16px;
    font-size: 20px;
    min-width: 50px;
    min-height: 50px;
    border-radius: 8px;
    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;
  }

  .size-display {
    padding: 12px 16px;
    font-size: 16px;
    border-radius: 8px;
  }

  .size-info {
    font-size: 14px;
    margin-bottom: 20px;
  }

  .mode-btn {
    padding: 12px 16px;
    font-size: 16px;
    min-height: 50px;
    border-radius: 8px;
    -webkit-tap-highlight-color: transparent;
    touch-action: manipulation;
  }
}